import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Layout from './components/Layout';
import Dashboard from './components/Dashboard';
import DropshipperDashboard from './components/DropshipperDashboard';
import MarketplacesPage from './pages/MarketplacesPage';
import DropshippingPage from './pages/DropshippingPage';
import ReportsPage from './pages/ReportsPage';
import SettingsPage from './pages/SettingsPage';
import UsersPage from './pages/UsersPage';
import PWAPrompt from './components/PWAPrompt';
import './App.css';

// User interface
interface User {
  id: string;
  username: string;
  email: string;
  role: 'super_admin' | 'admin' | 'dropshipper' | 'integrator' | 'support';
  permissions: string[];
  token: string;
}

// Get user data from global config
const getUserData = (): User | null => {
  try {
    const config = (window as any).MESCHAIN_CONFIG;
    if (config && config.userData) {
      return config.userData;
    }
    
    // Fallback mock data for development
    return {
      id: '1',
      username: 'admin',
      email: 'admin@meschain.com',
      role: 'admin',
      permissions: ['marketplace_manage', 'product_manage', 'order_manage', 'report_view'],
      token: 'mock-token'
    };
  } catch (error) {
    console.error('Error getting user data:', error);
    return null;
  }
};

const App: React.FC = () => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    // Initialize user data
    const userData = getUserData();
    if (userData) {
      setUser(userData);
    } else {
      setError('Kullanıcı verisi alınamadı');
    }
    setIsLoading(false);
  }, []);

  // Role-based route protection
  const hasPermission = (permission: string): boolean => {
    if (!user) return false;
    return user.permissions.includes(permission) || user.permissions.includes('all');
  };

  // Protected route component
  const ProtectedRoute: React.FC<{ 
    children: React.ReactNode; 
    permission?: string;
    allowedRoles?: string[];
  }> = ({ children, permission, allowedRoles }) => {
    if (!user) {
      return <Navigate to="/login" replace />;
    }

    if (permission && !hasPermission(permission)) {
      return (
        <div className="flex items-center justify-center min-h-screen">
          <div className="text-center">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">Erişim Reddedildi</h2>
            <p className="text-gray-600">Bu sayfaya erişim yetkiniz bulunmamaktadır.</p>
          </div>
        </div>
      );
    }

    if (allowedRoles && !allowedRoles.includes(user.role)) {
      return (
        <div className="flex items-center justify-center min-h-screen">
          <div className="text-center">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">Rol Yetkisi Gerekli</h2>
            <p className="text-gray-600">Bu sayfa sadece belirli roller için erişilebilir.</p>
          </div>
        </div>
      );
    }

    return <>{children}</>;
  };

  // Get appropriate dashboard based on user role
  const getDashboardComponent = () => {
    if (!user) return <Dashboard />;
    
    switch (user.role) {
      case 'dropshipper':
        return <DropshipperDashboard />;
      case 'super_admin':
      case 'admin':
      case 'integrator':
      case 'support':
      default:
        return <Dashboard />;
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-indigo-600 mx-auto"></div>
          <p className="mt-4 text-lg text-gray-600">MesChain-Sync Yükleniyor...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gray-50">
        <div className="text-center">
          <div className="text-red-500 text-6xl mb-4">⚠️</div>
          <h2 className="text-2xl font-bold text-gray-900 mb-4">Hata Oluştu</h2>
          <p className="text-gray-600 mb-4">{error}</p>
          <button 
            onClick={() => window.location.reload()}
            className="bg-indigo-600 hover:bg-indigo-700 text-white px-4 py-2 rounded-md transition-colors"
          >
            Sayfayı Yenile
          </button>
        </div>
      </div>
    );
  }

  return (
    <Router>
      <div className="App min-h-screen bg-gray-50">
        <Layout user={user}>
          <Routes>
            {/* Dashboard Route */}
            <Route 
              path="/" 
              element={
                <ProtectedRoute>
                  {getDashboardComponent()}
                </ProtectedRoute>
              } 
            />
            <Route 
              path="/dashboard" 
              element={
                <ProtectedRoute>
                  {getDashboardComponent()}
                </ProtectedRoute>
              } 
            />

            {/* Marketplaces Route */}
            <Route 
              path="/marketplaces" 
              element={
                <ProtectedRoute permission="marketplace_manage">
                  <MarketplacesPage />
                </ProtectedRoute>
              } 
            />

            {/* Dropshipping Route */}
            <Route 
              path="/dropshipping" 
              element={
                <ProtectedRoute permission="dropshipping_manage">
                  <DropshippingPage />
                </ProtectedRoute>
              } 
            />

            {/* Reports Route */}
            <Route 
              path="/reports" 
              element={
                <ProtectedRoute permission="report_view">
                  <ReportsPage />
                </ProtectedRoute>
              } 
            />

            {/* Settings Route */}
            <Route 
              path="/settings" 
              element={
                <ProtectedRoute permission="system_config">
                  <SettingsPage />
                </ProtectedRoute>
              } 
            />

            {/* Users Route (Super Admin only) */}
            <Route 
              path="/users" 
              element={
                <ProtectedRoute 
                  permission="user_manage"
                  allowedRoles={['super_admin']}
                >
                  <UsersPage />
                </ProtectedRoute>
              } 
            />

            {/* Catch all route */}
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </Layout>

        {/* PWA Prompt Component */}
        <PWAPrompt />
      </div>
    </Router>
  );
};

export default App; 